"use strict";

var render = require("../util/render");

var WorldMap = require("./world_map");
var GroupPie = require("./group_pie");
var ColorKey = require("../util/color_key");
var colors = require("../util/colors");

var Countries = function(app, repo) {
  this._app = app;
  this._repo = repo;
};

Countries.prototype = {
  _template: "<div class=\"card\">\n  <div class=\"pure-g\">\n    <div class=\"pure-u-1-3 pure-u-md-1-3 uk_outline\">\n      <img src=\"/img/uk.gif\" alt=\"The United Kingdom\" />\n    </div>\n    <div class=\"pure-u-2-3 pure-u-md-2-3 home_away_stats\">\n      <div class=\"home_away_pie_container\">\n      </div>\n\n      <div class=\"home_away_timeline_container\">\n      </div>\n\n      <div class=\"home_away_pullquote_container\">\n      </div>\n    </div>\n  </div>\n  <div class=\"pure-g\">\n    <div class=\"pure-u-1 pure-u-md-1 world_map\">\n      <div class=\"world_map_container\">\n      </div>\n    </div>\n  </div>\n  <div class=\"pure-g\">\n    <div class=\"pure-u-1 pure-u-md-1 country_details\">\n      <div class=\"country_summary_container\">\n      </div>\n    </div>\n  </div>\n</div>\n",

  _worldMapSelector: ".world_map_container",
  _homeAwayPieSelector: ".home_away_pie_container",

  render : function(selector) {
    render.toSelector(this._template, selector);
    var statistics = this._repo.getStatistic("Country");
    var worldMap = new WorldMap(this._repo, statistics);
    var homeAwayStats = statistics.last().oneVsAll("United Kingdom");
    var homeAwayColors = new ColorKey(homeAwayStats.keys(), [colors.complement, colors.neutral]);
    homeAwayStats = homeAwayStats.map(function(series, key) {
      return homeAwayColors.withColor(series, key);
    });
    var homeAwayPie = new GroupPie(homeAwayStats);
    worldMap.render(selector + " " + this._worldMapSelector);
    homeAwayPie.render(selector + " " + this._homeAwayPieSelector);
  }
};

module.exports = Countries;
