"use strict";

var bind = require("../util/bind");
var render = require("../util/render");

var dom = require("ampersand-dom");
var slick = require("slick");
var _ = require("underscore");
var events = require("dom-events");

var VisitableList = function(app, visitables) {
  this._app = app;
  this._visitables = visitables;
  this._app.registerMessageHandler("highlightPublisher", bind(this, this._onHighlightPublisher));
};

VisitableList.prototype = {

  _itemTemplate: "<li style=\"border-left-color: {{color}};\" class=\"publisher-{{publisher_id}} visitable clickable\">\n  <div class=\"title\">\n    {{title}}\n  </div>\n  <div class=\"subtitle\">\n    {{subtitle}}\n  </div>\n  <ul class=\"stats\">\n    <li class=\"visits_proportion\">\n      <span class=\"n\">{{visits_proportion}}</span> of all visits\n    </li>\n    <li class=\"visits\">\n      <span class=\"n\">{{visits}}</span> visits\n    </li>\n    <li class=\"views\">\n      <span class=\"n\">{{views}}</span> views\n    </li>\n  </ul>\n</li>\n",

  render: function(selector) {
    var container = slick.find(selector);
    this._element = document.createElement("ul");
    dom.addClass(this._element, "visitable_list");
    container.appendChild(this._element);
    this._renderVisitables();
  },

  _renderVisitables: function() {
    dom.text(this._element, "");
    _.each(this._visitables, bind(this, function(visitable) {
      var child = render.toNode(this._itemTemplate, this._templateParameters(visitable));
      this._element.appendChild(child);
      events.on(child, "mouseover", bind(this, function() { this._onHover(visitable); }));
      events.on(child, "click", bind(this, function(event) { event.preventDefault(); this._onClick(visitable); }));
    }));
  },

  _templateParameters: function(item) {
    return {
      title: item.title(),
      subtitle: item.subtitle(),
      visits_proportion: (item.visitsProportion() * 100).toFixed(1) + "%",
      visits: item.visits(),
      views: item.views(),
      color: item.color(),
      publisher_id: item.publisher().id()
    }; 
  },

  update: function(visitables) {
    this._visitables = visitables;
    this._renderVisitables();
  },

  //FIXME this is publisher-specific, not for all visitables. extract.
  _onClick: function(visitable) {
    this._app.sendMessage("selectPublisher", visitable.publisher());
  },

  _onHover: function(visitable) {
    this._app.sendMessage("highlightPublisher", visitable.publisher());
  },

  _onHighlightPublisher: function(publisher) {
    var allElements = slick.search(".visitable", this._element);

    _.each(allElements, function(element) {
      dom.removeClass(element, "highlighted"); 
      dom.removeClass(element, "not-highlighted");
      if(publisher) {
        if(dom.hasClass(element, "publisher-" + publisher.id())) {
          dom.addClass(element,"highlighted");
        } else {
          dom.addClass(element,"not-highlighted");
        }
      }
    });
  },
};

module.exports = VisitableList;
